import { useEffect, useRef } from "react";

import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import { projects } from "./projects";

import { ProjectsItem } from "../projectsItem/ProjectsItem";

import style from "./ProjectsList.module.scss";

gsap.registerPlugin(ScrollTrigger);

export const ProjectsList = ({ section }) => {
  const panelsWrapperRef = useRef(null);
  const isMobile = useRef(
    typeof window !== "undefined" && window.innerWidth < 768
  );

  useEffect(() => {
    if (!panelsWrapperRef.current || !section.current) return;

    const container = panelsWrapperRef.current;
    const list = container.querySelector("ul");
    const totalWidth = list.scrollWidth;
    const viewportWidth = section.current.offsetWidth;

    gsap.set(container, { x: 0, clearProps: "all" });

    const tween = gsap.to(container, {
      x: () => -(totalWidth - viewportWidth),
      ease: "none",
      scrollTrigger: {
        id: "projects-scroll",
        trigger: section.current,
        pin: true,
        pinSpacing: true,
        start: isMobile.current ? "top bottom" : "top top",
        end: () => `+=${totalWidth}`,
        scrub: 1,
        invalidateOnRefresh: true,
        anticipatePin: 1,
        onRefresh: (self) => {
          gsap.set(container, { x: 0 });
        },
        onUpdate: (self) => {
          if (isMobile.current && self.direction === -1) {
            self.scroll();
          }
        },
      },
    });

    setTimeout(() => {
      ScrollTrigger.refresh();
    }, 300);

    const handleResize = () => {
      isMobile.current = window.innerWidth < 768;
      setTimeout(() => ScrollTrigger.refresh(), 100);
    };

    window.addEventListener("resize", handleResize);
    window.addEventListener("orientationchange", handleResize);

    return () => {
      tween?.kill();
      ScrollTrigger.getById("projects-scroll")?.kill();
      window.removeEventListener("resize", handleResize);
      window.removeEventListener("orientationchange", handleResize);
    };
  }, [section]);

  return (
    <div className={style.projectsListWrapper} ref={panelsWrapperRef}>
      <ul className={style.projectsList}>
        {projects.map((project) => (
          <ProjectsItem
            section={section}
            key={project.id}
            image={project.image}
            id={project.id}
            title={project.title}
            description={project.description}
            href={project.href}
          />
        ))}
      </ul>
    </div>
  );
};
